/* TRAVEL-OPTIONS-BOX */
.travel-options-box-container {
  width: 100%;
  max-height: 60vh;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: row;
  grid-gap: 20px 0;
  gap: 20px 0;
  margin-bottom: 50px;
  cursor: pointer;
}

.relative {
  position: relative;
}

.travel-options-box {
  min-width: 36vw;
  height: 28vh;
  /* background-color: rgb(177, 85, 48); */
  background-color: var(--corn-black);
  border-radius: 20px;
  padding: 10px 15px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  border: 1px solid var(--corn-blue);


  transition: transform 0.3s ease;
}

.travel-options-box::after, .travel-options-box::before {
  content: "";
  position: absolute;
  opacity: 0.3;
  background: var(--corn-black);
  border-radius: inherit;
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0;
  z-index: -1;
  transition: transform 0.3s ease;
}

.travel-options-box:hover {
  transform: translate(-25px, -9px);
}

.travel-options-box:hover::after {
  transform: translate(12px, 12px);
}

.travel-options-box h2 {
  font-size: 52px;
  font-weight: 600;
  color: var(--corn-green-bright);
}

.flight-duration-flex {
  display: flex;
  flex-direction: column;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fill-available;
  margin: 10px;
  text-align: center;
  /* margin-top: 7%; */
}

.flight-line {
  width: -webkit-fill-available;
  height: 3px;
  background: #444442;
  align-self: center
}

/* BUY_BTN */
.buy-btn {
  text-align: center;
  display: inline-block;
  background-color: #fff;
  /* width: 3em; */
  padding: 6px 12px;
